@use '../../foundation/variable' as v;

// 
.c-anim-shutter {
	opacity: 1;

	&::before {
		position: absolute;
		content: '';
		display: block;
		width: 100%;
		height: 100%;
		background: url('../img/common/rabbit.svg');
		background-color: rgba(v.$color--moon-main, 0.5);
		filter: blur(5px);
		z-index: 10;
	}

	&.show {
		&::before {
			animation: toRight 1s both; //
			// animation: down 2s both; //

			@for $i from 1 to 4 {
				&:nth-child(#{$i}) {
					animation-delay: 0.5s * $i; //
					// animation-delay: 500s * $i; //
				}
			}
		}
	}
}

@keyframes toRight {
	0% {
		opacity: 1;
		transform: translate(0, 0);
	}
	50% {
		opacity: 1;
		transform: translate(0, 0);
	}
	100% {
		opacity: 0;
		transform: translate(300px, 0);
	}
}
